.card-list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 360px));
  column-gap: 30px;
  row-gap: 48px;
  padding: 0 70px;
  justify-content: center;
}

@media screen and (max-width: 600px) {
  .card-list {
    padding: 0 10px;
    gap: 40px;
  }
}