.footer__footer {
  border-top: 1px solid #424242;
  display: flex;
  justify-content: space-between;
  color: #fff;
  padding: 20px 0;
}

@media screen and (max-width: 600px) {
  .footer__footer {
    flex-direction: column-reverse;
    align-items: center;
    padding: 30px 0 10px;
  }
}