.techs__technology {
  max-width: 90px;
  padding: 21px 0;
  background-color: #303030;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  letter-spacing: -0.04em;
}

.techs__technology:hover {
  opacity: .8;
}

@media screen and (max-width: 900px) {
  .techs__technology{
    max-width: 84px;
    font-size: 12px;
    line-height: 15px;
  }
}